.google-button {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    padding: 10px 20px;
    background-color: #db4437;
    color: #fff;
    border: none;
    border-radius: 5px;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
    transition: background-color 0.3s ease;
}
h3{
    color: teal;
    text-align: center;
}
.google-button:hover {
    background-color: #c33227;
}

.google-button img {
    width: 20px;
    height: 20px;
    margin-right: 10px;
}