body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  font-weight: 500;
}

.App {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100vh;
  flex-direction: column;
}

.login-container {
  display: flex;
  max-width: 800px;
  margin: 0 auto;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

.login-container img {
  width: 50%;
  object-fit: cover;
  border-radius: 5px 0 0 5px;
}

.login-content {
  flex: 1;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

h1 {
  color: teal;
  font-size: 24px;
  margin-bottom: 1rem;
  text-align: center;
}

form {
  display: flex;
  flex-direction: column;
}

input {
  padding: 12px 20px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  outline: none;
  transition: 0.3s;
}

input:focus {
  transform: translateY(-10px);
}

input:not(:placeholder-shown)+label {
  transform: translateY(-20px);
  font-size: 12px;
  color: teal;
}

form button {
  padding: 12px 20px;
  border-radius: 5px;
  background-color: teal;
  border: none;
  outline: none;
  cursor: pointer;
  color: white;
  font-size: 16px;
  transition: background-color 0.3s ease;
}

form button:hover {
  background-color: #006f9c;
}

@media (max-width: 768px) {
  .login-container {
    flex-direction: column;
    max-width: 100%;
  }

  .login-container img {
    width: 100%;
    border-radius: 5px 5px 0 0;
  }
}

.active {
  background-color: rgba(255, 68, 0, 0.363);
}

.footer {
  background-color: teal;
  color: white;
  font-size: 16px;
  text-align: center;
  position: sticky;
  bottom: 0;
  width: 100%;
  padding: 8px 0;
}

.invalid {
  background-color: #00709c62;
  color: white;
}

.logout {
  padding: 12px 20px;
  border-radius: 5px;
  background-color: teal;
  border: none;
  outline: none;
  cursor: pointer;
  color: white;
  font-size: 16px;
  transition: background-color 0.3s ease;
}
p{
  text-align: left;
  font-size: 12px;
  line-height: 18px;
  color: red;
  margin: 2px;
  padding: 0;
}
input::placeholder{
  color: black;
  font-weight: 500;
}